.category-container{
    margin: 10px 20px;
    width: 200px;
    height: 300px;
    padding: 0 10px;
    box-sizing: border-box;
    font-size: 25px;
    .category{
        width: 100%;
        height: 100%;
        background-size: cover;
        background-position: center; 
        div{
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: #0000005a;
        color: #fff;
        width: 100%;
        height: 100%;    
        font-weight: 600;
        transition: 0.5s ease;
        text-align: center;
        }
        
        cursor: pointer;
    }
    .category:hover{
        font-size: 30px;
    }
    animation: opa 1s;
}
@keyframes opa { 
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }